@import '../../style/_vars.scss';

//---------HEADER----------
.header {
    padding: 50px 5px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

.logo {
    font-size: 28px;
    line-height: 33px;
    font-weight: 700;
    font-family: 'Roboto Condensed';
}

.header-links {
    &__link {
        font-size: 0.85rem;
        line-height: 28px;
        font-weight: 700;
        margin: 0 5px;
        color: #000;
        transition: all 0.3s ease;

        &:hover {
            cursor: pointer;
            color: $main-color;
        }
    }
}

@media (max-width: 500px) {
    .header {
        flex-direction: column;
        gap: 10px;
        padding: 25px 5px;
    }
}

.active_header {
    color: $main-color;
}